<script>
export default {
  name: "BigModalTen",
  props: ["isShow"],
  data() {
    return {
      open: false,
    };
  },
  created() {
    this.open = this.isShow;
    const body = document.querySelector("body");
    const lampPanel = document.getElementsByClassName("lamp-panel-wrapper")[0]
    body.style.overflow = "hidden";
    lampPanel.style.display = "none";
  },
  methods: {
    hideModal() {
      this.open != this.open;
      const body = document.querySelector("body");
      const lampPanel = document.getElementsByClassName("lamp-panel-wrapper")[0]
